'use client';

import { motion } from 'framer-motion';

const cardVariants = {
  hidden: { opacity: 0, y: 40 },
  visible: (i: number) => ({
    opacity: 1,
    y: 0,
    transition: {
      delay: i * 0.2,
      type: 'spring',
      stiffness: 180,
      damping: 16,
    },
  }),
};

const features = [
  {
    title: 'Fast Conversion',
    description: 'Convert MP4 files to MP3 quickly and efficiently with high-quality results.',
  },
  {
    title: 'No Signup Needed',
    description: 'Use the converter for free up to 3 times per week. No login or signup required!',
  },
  {
    title: 'High-Quality Output',
    description: 'MP3 files are optimized for the best audio quality.',
  },
  {
    title: 'Privacy & Security',
    description: 'We value your privacy. Your files are deleted after conversion.',
  },
];

export default function Features() {
  return (
    <section
      id="features"
      className="max-w-7xl mx-auto px-4 py-10 sm:py-14 md:py-20 scroll-mt-20"
    >
      {/* Heading */}
      <motion.h2
        initial={{ opacity: 0, y: -30 }}
        whileInView={{ opacity: 1, y: 0 }}
        viewport={{ once: true }}
        transition={{ duration: 0.6, type: 'spring', stiffness: 120 }}
        className="text-4xl md:text-5xl font-semibold text-center text-gray-900 dark:text-white mb-12"
      >
        Features
      </motion.h2>

      {/* Features Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-2 gap-10">
        {features.map((feature, i) => (
          <motion.div
            key={i}
            custom={i}
            variants={cardVariants}
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, amount: 0.2 }}
            className="group rounded-3xl p-8 bg-white/80 dark:bg-zinc-800/60 border border-gray-200 dark:border-zinc-700 shadow-lg hover:shadow-2xl backdrop-blur-sm transform transition-all duration-300 hover:-translate-y-1 hover:scale-[1.015]"
          >
            <h3 className="text-2xl font-semibold text-gray-800 dark:text-white mb-3 group-hover:text-blue-600 transition-colors duration-300">
              {feature.title}
            </h3>
            <p className="text-gray-600 dark:text-gray-300 text-base leading-relaxed">
              {feature.description}
            </p>
          </motion.div>
        ))}
      </div>
    </section>
  );
}
